<template>
  <div class="artist">
    <div class="header" @click="back">
      <img src="../assets/images/reback.png" alt class="auto-img" />
    </div>
    <div class="hd" v-if="artistData.artist">
      <img :src="artistData.artist.picUrl" class="auto-img" />
      <div class="artist-info">
        <p class="name">{{artistData.artist.name}}</p>
      </div>
    </div>
    <HomeTitle>歌手简介</HomeTitle>
    <div class="intro" v-if="artistData.artist">
      <p :class="{fthide2:isShowf}" style="color:#666">{{artistData.artist.briefDesc}}</p>
      <div class="full" @click="clickShow" v-if="!isShow">
        完整歌手介绍
        <i :class="{arrowup: isShow}" class="arrow"></i>
      </div>
      <div class="full" @click="clickShow" v-else>
        收起
        <i :class="{arrowup: isShow}" class="arrow"></i>
      </div>
    </div>
    <HomeTitle>热门单曲</HomeTitle>
    <ul class="hot-item">
      <Songsheetlistitem
        v-for="(item,index) in artistData.hotSongs"
        :key="index"
        :item="item"
        :number="index"
        @translate-currentid="$emit('translate-currentid',$event)"
      ></Songsheetlistitem>
    </ul>
  </div>
</template>

<script>
import "../assets/less/artist.less";
import HomeTitle from "../components/Hometitle";
import Songsheetlistitem from "../components/Songsheetlistitem";
export default {
  name: "Artist",
  components: {
    HomeTitle,
    Songsheetlistitem,
  },
  data() {
    return {
      id: "",
      artistData: [],
      isShow: false,
      isShowf: true,
    };
  },
  created() {
    this.id = this.$route.query.id;
    

    this.getArtistData(this.id);
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    //获取歌手信息
    getArtistData(id) {
      this.artistData = [];
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "http://music.kele8.cn/artists?id=" + id,
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 200) {
            this.artistData = result.data;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //完整歌手介绍
    clickShow() {
      this.isShow = !this.isShow;
      this.isShowf = !this.isShowf;
    },
  },
};
</script>

<style lang="less" scoped>
</style>